
#map-section {
  padding-bottom: 0;
}

#compass {
  position:relative;
  padding: 1.2em;
  border-radius: 100%;
  /* clip-path: circle(50%); */
  font-size: 1.5em;
  font-weight: 900;
  /* background-color: var(--infoColor); */
}

.map-overlay {
  position: absolute;
  width: calc(100% - 2.4em);
  height: calc(100% - 2.4em);
  z-index: 2;
  pointer-events: none;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 100%;
  margin:auto;
}

#compass-ticks {
  background-image: url('vector/compass-ticks.svg');
  background-size: 95%;
}

#wind-arrow {
  background-image: url('vector/wind-arrow3.svg');
  background-size: 90%;
  transform:rotate(69deg);
}

#waves-arrow {
  background-image: url('vector/waves-arrow3.svg');
  background-size: 90%;
  transform: rotate(65deg);
}

#map {
  width: 100%;
  padding-bottom: 100%;
  border-radius: 100%;
  clip-path: circle(50%);

  /*tmp*/
  background-color: red;
  background-image: url('img/map-img.png');
  background-size: 100%;
}

.mapboxgl-control-container {
  visibility: hidden;
}

.cardinal-grid {
  width: 100%;
  height: 100%;
  position: absolute;
  top:0px;
  left:0px;
  display: grid;
  grid-template-columns: 30% 40% 30%;
  grid-template-rows: 30% 40% 30%;
}

#cardinals-secondary {
  font-size: .8em;
  font-weight: 100;
  transform: rotate(-45deg);
}

.cardinal-point {
  margin: .1em;
  width: 1em;
  height: 1em;
  border-radius: 100%;
  text-align: center;
  
  line-height: .95em;

  /* background-color: pink; */
}
#cardinals-secondary > .cardinal-point {
  margin: .2em;
}

.cardinal-point > span {
  display: inline-block;
  margin-left: -100%; /* align center oveflowing text trick */
  margin-right: -100%;
  text-align: center;
}

#cardinals-primary > .cardinal-point {
  /* todo */
}

#cardinals-secondary > .cardinal-point {
  transform: rotate(45deg);
}


.cardinal-top {
  grid-row: 1;
  grid-column: 2;
  align-self: start;
  justify-self: center;
}
.cardinal-left {
  grid-row: 2;
  grid-column: 1;
  align-self: center;
  justify-self: left;
}
.cardinal-right {
  grid-row: 2;
  grid-column: 3;
  align-self: center;
  justify-self: right;
}
.cardinal-down {
  grid-row: 3;
  grid-column: 2;
  align-self: end;
  justify-self: center;
}

